<template>
	<div class="sec_bg">
	<section>
		<div class="title">
			<p> &nbsp;: &nbsp;<span>-</span></p>
			<p> &nbsp;: &nbsp;<span>32441707071639208331</span></p>
			<p>: &nbsp;<span>0.01</span></p>
		</div>
		<div class="pay_content" v-show="show">
			<div class="pay_way">
				<h1></h1>
				<ul>
					<li v-for="(i,index) in list" :key="index" @click="changeIndex(index)" :class="liIndex==index?'li-active':''">
						<img :src="i.src" alt="" />
						<span>{{i.payway}}</span>
					</li>
				</ul>
			</div>
			<div class="paybox">
				<p>:0.01</p>
				<router-link to="">
					<div @click="showCon"></div>
				</router-link>
			</div>
			<div class="pay_word">
				<p><span>*</span></p>
				<p><span>*</span></p>
			</div>
			<div class="layout" v-show="layout">
				<p></p>
				<p><span>{{mytime}}</span></p>
				<!--<span @click="times()">k</span>-->
			</div>
		</div>
		<div class="wx_pay" v-show="wx">
			<h1><img src="../../assets/img/demo/demo_pc_wechat.png" alt="" /></h1>
			<h2> &nbsp;[] &nbsp;</h2>
			<div class="img_box">
				<!--<img src="../../assets/img/demo/demo_pc_ewm.png" alt="" />-->
				<div id="qrcode">
			        <div id="codeico"></div>
			    </div>
				<img src="../../assets/img/demo/demo_pc_wechat_phone.png" alt="" />
			</div>
			<p @click="showOther()"> &nbsp;></p>
		</div>
		<div class="yl_pay" v-show="yl">
			<h1><img src="../../assets/img/demo/demo_pc_yl.png" alt="" /></h1>
			<div><i></i><input type="number" placeholder=""/></div>
			<div><i></i><input type="tel" placeholder=""/></div>
			<div><i></i><input type="text" placeholder=""/><span></span></div>
			<button></button>
			<p @click="showOther()"> &nbsp;></p>
		</div>
		<div class="ali_pay" v-show="ali">
			<h1><img src="../../assets/img/demo/demo_pc_ali.png" alt="" /></h1>
			<h2> &nbsp;[] &nbsp;</h2>
			<div class="img_box">
				<img src="../../assets/img/demo/demo_pc_ewm.png" alt="" />				
				<img src="../../assets/img/demo/demo_pc_ali_phone.png" alt="" />
			</div>
			<p @click="showOther()"> &nbsp;></p>
		</div>
		
		
	</section>
	</div>
</template>

<script>
	import $ from '../../../static/libs/jquery-1.10.1.min.js';
	import '../../../static/libs/code.js';
	export default {
  		data () {
   		 return {
   		 	list:[
   		 		{src:require("../../assets/img/demo/demo_pc_wechat.png"),payway:""},
   		 		{src:require("../../assets/img/demo/demo_pc_yl.png"),payway:""},
   		 		{src:require("../../assets/img/demo/demo_pc_ali.png"),payway:""}
   		 	],
   		 	liIndex:0,
   		 	show:true,
   		 	wx:false,
   		 	yl:false,
   		 	ali:false,
   		 	layout:false,
   		 	mytime:5,   		 	
   		 }
  		},
  		computed:{
  			
  		},
  		created(){
			this.init()
			
  		},
  		methods:{
  			changeIndex(index){
  				console.log(index);
				this.liIndex=index;
				console.log(this.liIndex);
  			},
  			//v-show
			showCon(){
  				console.log(this.show)
				this.show=!this.show;
				console.log(this.liIndex)
				if(this.liIndex==0){
					this.wx=true
				}
				if(this.liIndex==1){
					this.yl=true
				}
				if(this.liIndex==2){
					this.ali=true
				}
				console.log(this.wx);
				console.log(this.yl);
				console.log(this.ali);
  			},
  			//v-show
  			showOther(){
  				this.show=true;
  				this.wx=false;
  				this.yl=false;
  				this.ali=false;
  			},
  			//
  			times(){
  				var that=this;
  				var interval=setInterval(function(){  					
  					console.log(that.mytime)
  					if((that.mytime--)<=0){
  						that.layout=false; 						
  						clearInterval(interval);
  					} 					
  				},1000)
  			},
		  	generateQRCode(rendermethod, picwidth, picheight, url) {
		  		
		  		let that = this
		  		let text = that.utf16to8(url)
		  		this.$nextTick(()=>{
		  			$("#qrcode").qrcode({ 
			            render: rendermethod, // tableIEcanvas
			            width: picwidth, // 
			            height:picheight, // 
			            text: text, // 
			            typeNumber:-1,//
			            correctLevel:2,//
			            background:"#ffffff",//
			            foreground:"#000000"  //
		        	})
		  		})
		        
		       
		   },
		   init() {
		        this.generateQRCode("table",200, 200, "http://wwww.baidu.com");		        
		   },
		   utf16to8(str) {
		        var out, i, len, c;
		        out = "";
		        len = str.length;
		        for (i = 0; i < len; i++) {
		            c = str.charCodeAt(i);
		            if ((c >= 0x0001) && (c <= 0x007F)) {
		                out += str.charAt(i);
		            } else if (c > 0x07FF) {
		                out += String.fromCharCode(0xE0 | ((c >> 12) & 0x0F));
		                out += String.fromCharCode(0x80 | ((c >> 6) & 0x3F));
		                out += String.fromCharCode(0x80 | ((c >> 0) & 0x3F));
		            } else {
		                out += String.fromCharCode(0xC0 | ((c >> 6) & 0x1F));
		                out += String.fromCharCode(0x80 | ((c >> 0) & 0x3F));
		            }
		        }
		        return out;
		    } 
			
  		}
}
</script>

<style>
</style>


