<template>
	<div class="mobile_wrap">
		<header>
			<img src="../../assets/img/demo/demo_h5_logo.png" alt="" />
		
		</header>
		<div class="pay_info">
			<h1>0.01</h1>
			<h2>-</h2>
			<h3>32441707071639208331</h3>
		</div>
		<div class="pay_way">
			<ul>
				<li v-for="(item,index) in imgList" :key="index" @click="changeIndex(index)" :class="liIndex==index?'active':''">
					<img :src="item.src" alt="" />
					<span>{{item.payway}}</span>
					<img :src="item.icon" alt="" />
				</li>
			</ul>
		</div>
		<button @click="pay()">0.01</button>
		<div class="pay_word">
				<p><span>*</span></p>
				<p><span>*</span></p>
		</div>
		<div class="shadow" v-show="shadow">
			<div class="alert" >
				<div class="del" @click="del()"></div>
				<h1></h1>
				<button @click="del()"></button>
			</div>
		</div>
	</div>
</template>

<script>
import  '../../../static/flexible/flexible.js';
import $ from '../../../static/libs/jquery-1.10.1.min.js';
import router from "../../router/index.js";
//import  '../../../static/layer_mobile/layer.js';
export default {
  		data () {
	   		 return {
	   		 	imgList:[
	   		 		{src:require("../../assets/img/demo/demo_pc_wechat.png"),payway:"",icon:require("../../assets/img/demo/demo_h5_check.png")},  				
	   		 		{src:require("../../assets/img/demo/demo_pc_ali.png"),payway:"",icon:require("../../assets/img/demo/demo_h5_check.png")},
	   		 		{src:require("../../assets/img/demo/demo_pc_yl.png"),payway:"",icon:require("../../assets/img/demo/demo_h5_check.png")}
	   		 	],
	   		 	liIndex:0,
	   		 	shadow:false,
	   		 	
	   		 	
	   		 }
   	},
   	methods:{
   		changeIndex(index){
  				console.log(index);
				this.liIndex=index;
				console.log(this.liIndex);
  			},
  			pay(){
  				if(this.liIndex==2){
  					router.push("/bank");
  				}
//				this.$layer.alert('...',2,1)
  			},
  			del(){
  				this.shadow=false;
  			}
   	},
   	created() {
   		

   	}
}
	
</script>

