<template>
<div class="active_wrap">
	<top></top>
	<section v-if="active">
		<img src="../../assets/img/login_register/login_email.png" alt="" />
		<h1></h1>
		<p>{{user}}<span>48</span></p>
		<p><span></span><router-link to="/login"></router-link></p>
	</section>
	<section v-if="active1">
		<img src="../../assets/img/login_register/login_sucess.png" alt="" />
		<h1></h1>
		<p></p>
		<p><span><span id="time">{{mytime}}</span>s</span><router-link to="/login"></router-link></p>
	</section>
	<foot></foot>
</div>
</template>

<script>
	import foot from '../register/foot';
	import top from '../register/head';
	import router from "../../router/index.js";
export default{
	data(){
		return{
			user:this.$route.query.user,
			active:false,
			active1:true,
			mytime:5
		}
	},
	mounted(){
		this.times();
	},
	methods:{
		times(){
  				var that=this;
  				var interval=setInterval(function(){  					
  					console.log(that.mytime)
  					if((that.mytime--)<=0){
  						 						
  						clearInterval(interval);
  						router.push({path:"/login",query:{}})
  					} 					
  				},1000)
  			}
	},
	components:{
		foot,
		top
	}
}
</script>
	
<style>
</style>


